body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: hsl(0, 0%, 100%);
  background-color: hsl(261, 57%, 17%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.To-do-list{
  font-weight: bold;
  display: flex;
  align-items: center;
  padding: 5px;
  display: flex;flex-direction: column;
}
.top{
  display: flex;
  justify-content: center;
}
.ko{
  padding: 0.5rem;
  border-radius: 0.3rem 0rem 0rem 0.3rem;
  border: black;

}
.Add{
  padding: 0.5rem 1.3rem;
  font-weight: bolder;
  background-color: rgb(35, 198, 87);
  color:hsl(261, 57%, 17%);
  border: none;
  cursor: pointer;
  border-radius: 0rem 0.3rem 0.3rem 0rem;
  transition: 0.3s;
}
.Add:hover{
  background-color: hsla(102, 30%, 24%, 0.734);
  color: white;
}
ol{
  padding: 0;
  list-style: none;
}
li{
  box-shadow: 100px;
  background-color: hsla(0, 0%, 100%, 0.925);
  padding: 0.5rem;
  display: flex;
  font-size: small;
  align-items: center;
  margin: 10px 2px;
  color:hsl(261, 57%, 17%);
  border-radius: 0.3rem;
}
.text{
  flex: 1;
}
.delete{
  border: none;
  border-radius: 0.2rem;
  padding: 0.3rem 0.6rem;
  font-size: smaller;
  cursor: pointer;
  background-color: rgb(214, 1, 1);
  color: white;
  font-weight: bold;
  transition: 0.3s;
}
.delete:hover{
  background-color: rgb(95, 25, 25);
}


